import React, { useState, useRef, useEffect, useCallback, useMemo, memo } from 'react';
import ErrorBoundary from '../common/ErrorBoundary';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useFocusTrap } from '../../hooks/useFocusTrap';

const FloatingModal = styled(motion.div).attrs({
  role: 'dialog',
  'aria-modal': true,
  'aria-labelledby': 'chat-title'
})`
  position: fixed;
  right: ${props => props.theme.spacing.xl};
  bottom: ${props => props.theme.spacing.xl};
  width: 360px;
  background: ${props => props.theme.colors.secondary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: ${props => props.theme.borderRadius.xl};
  overflow: hidden;
  box-shadow: ${props => props.theme.shadows.lg};
  backdrop-filter: blur(20px);
  
  @media (max-width: 768px) {
    width: calc(100% - ${props => props.theme.spacing.xl} * 2);
    right: ${props => props.theme.spacing.lg};
    bottom: ${props => props.theme.spacing.lg};
    max-height: 80vh;
  }
`;

const ModalHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: ${props => props.theme.spacing.lg};
  background: ${props => props.theme.colors.tertiary};
  border-bottom: 1px solid ${props => props.theme.colors.glass.border};
  
  h2 {
    color: ${props => props.theme.colors.text.primary};
    font-size: ${props => props.theme.typography.fontSize.xl};
    font-weight: ${props => props.theme.typography.fontWeight.semibold};
  }
`;

const CloseButton = styled(motion.button)`
  background: none;
  border: none;
  color: ${props => props.theme.colors.text.secondary};
  cursor: pointer;
  padding: ${props => props.theme.spacing.xs};
  
  &:hover {
    color: ${props => props.theme.colors.text.primary};
  }
`;

const ModalContent = styled.div`
  padding: ${props => props.theme.spacing.lg};
  max-height: 400px;
  
  @media (max-width: 768px) {
    max-height: calc(80vh - 120px); // Account for header and input
  }
  overflow-y: auto;
  
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: ${props => props.theme.colors.glass.primary};
  }
  
  &::-webkit-scrollbar-thumb {
    background: ${props => props.theme.colors.glass.border};
    border-radius: ${props => props.theme.borderRadius.full};
  }
`;

const MessageBase = styled.article`
  display: flex;
  gap: ${props => props.theme.spacing.md};
  margin-bottom: ${props => props.theme.spacing.md};
  
  .avatar {
    width: 32px;
    height: 32px;
    border-radius: ${props => props.theme.borderRadius.full};
    background: ${props => props.theme.colors.glass.secondary};
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .content {
    flex: 1;
    background: ${props => props.theme.colors.glass.secondary};
    padding: ${props => props.theme.spacing.md};
    border-radius: ${props => props.theme.borderRadius.lg};
    color: ${props => props.theme.colors.text.primary};
  }
  
  &.sent {
    flex-direction: row-reverse;
    
    .content {
      background: ${props => props.theme.colors.accent};
      color: ${props => props.theme.colors.primary};
    }
  }
`;

const Input = styled.input.attrs({
  'aria-label': 'Type your message'
})`
  width: 100%;
  background: ${props => props.theme.colors.tertiary};
  border: 1px solid ${props => props.theme.colors.glass.border};
  border-radius: ${props => props.theme.borderRadius.lg};
  padding: ${props => props.theme.spacing.md};
  color: ${props => props.theme.colors.text.primary};
  margin-top: ${props => props.theme.spacing.lg};
  
  &:focus {
    outline: none;
    border-color: ${props => props.theme.colors.text.accent};
    box-shadow: ${props => props.theme.shadows.accent};
  }
  
  &::placeholder {
    color: ${props => props.theme.colors.text.secondary};
  }
`;

const OnlineUsers = styled.div`
  display: flex;
  align-items: center;
  gap: ${props => props.theme.spacing.xs};
  font-size: ${props => props.theme.typography.fontSize.sm};
  color: ${props => props.theme.colors.text.secondary};
  margin-left: ${props => props.theme.spacing.md};
`;

const OnlineIndicator = styled.div`
  width: 8px;
  height: 8px;
  background: ${props => props.theme.colors.success};
  border-radius: 50%;
`;

const modalVariants = {
  hidden: { opacity: 0, y: 20, scale: 0.95 },
  visible: { 
    opacity: 1, 
    y: 0, 
    scale: 1,
    transition: { type: "spring", stiffness: 300, damping: 30 }
  },
  exit: { opacity: 0, y: 20, scale: 0.95 }
};

const Message = memo(({ message }) => (
  <MessageBase
    className={message.isUser ? 'sent' : ''}
    aria-label={`${message.isUser ? 'You' : 'AI Assistant'} said: ${message.text}`}
  >
    <div className="avatar">
      {message.isUser ? 'ðŸ‘¤' : 'ðŸ¤–'}
    </div>
    <div className="content">
      {message.text}
    </div>
  </MessageBase>
));

Message.displayName = 'Message';

const Chat = ({ isOpen, onClose }) => {
  const modalRef = useRef(null);
  useFocusTrap(modalRef, isOpen);
  const [messages, setMessages] = useState([
    {
      id: 1,
      text: "Welcome to the AI Careers chat! Feel free to ask questions or discuss with other attendees.",
      isUser: false,
      timestamp: new Date()
    }
  ]);
  const [inputValue, setInputValue] = useState('');
  const [onlineCount] = useState(23);
  const messagesEndRef = useRef(null);

  const responses = useMemo(() => [
    "That's a great question! Let me think about that...",
    "I agree! The AI field is evolving so rapidly.",
    "Thanks for sharing your perspective!",
    "That's an interesting point about AI careers.",
    "I've had similar experiences in the field."
  ], []);

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = useCallback(() => {
    if (inputValue.trim() === '') return;

    const newMessage = {
      id: messages.length + 1,
      text: inputValue,
      isUser: true,
      timestamp: new Date()
    };

    setMessages([...messages, newMessage]);
    setInputValue('');

    setTimeout(() => {
      const responseMessage = {
        id: messages.length + 2,
        text: responses[Math.floor(Math.random() * responses.length)],
        isUser: false,
        timestamp: new Date()
      };
      
      setMessages(prev => [...prev, responseMessage]);
    }, 1000 + Math.random() * 2000);
  }, [inputValue, messages, responses]);

  const handleKeyPress = useCallback((e) => {
    if (e.key === 'Enter') {
      handleSendMessage();
    }
  }, [handleSendMessage]);

  if (!isOpen) return null;

  return (
    <ErrorBoundary>
    <FloatingModal
      ref={modalRef}
      variants={modalVariants}
      initial="hidden"
      animate="visible"
      exit="exit"
    >
      <ModalHeader>
        <h2 id="chat-title">
          ðŸ’¬ Live Chat
          <OnlineUsers>
            <OnlineIndicator />
            {onlineCount} online
          </OnlineUsers>
        </h2>
        <CloseButton
          onClick={onClose}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          aria-label="Close chat"
        >
          Ã—
        </CloseButton>
      </ModalHeader>

      <ModalContent role="log" aria-live="polite" aria-label="Chat messages">
        <AnimatePresence>
          {messages.map((message) => (
            <Message key={message.id} message={message} />
          ))}
        </AnimatePresence>
        <div ref={messagesEndRef} />
      </ModalContent>

      <div style={{ padding: '1rem' }}>
        <Input
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder="Type your message..."
        />
      </div>
    </FloatingModal>
    </ErrorBoundary>
  );
};

export default Chat;